.App {
  display: flex;
  align-items: center;
  flex-direction: column;
  margin: auto;
}

button {
  display: inline-block;
  border: 1px solid #0366ee;
  border-radius: 4px;
  background: #0366ee;
  color: #ffffff;
  font-weight: 600;
  font-family: Arial, sans-serif;
  font-size: 1rem;
  padding: 0.75rem 1.25rem;
  margin: 0.3rem;
  vertical-align: middle;
  text-align: center;
  cursor: pointer;
}

button:active,
button:hover {
  border: 1px solid #0250bc;
  background: #0250bc;
  color: #ffffff;
}
input {
  display: block;
  border: 1px solid #dedede;
  border-radius: 4px;
  padding: 0.75rem;
  background: white;
  margin-bottom: 0.5rem;
  font-size: 1rem;
}
